.title {
  margin: 0;
  padding-top: 27px;
  font-size: 36px;
  font-weight: 400;
  line-height: 36px;
  font-family: "Oswald", "Arial", sans-serif;
  color: @black;

  @media (min-width: @tablets) {
    font-size: 60px;
    line-height: 60px;
    padding: 65px 70px 30px;
  }

  @media (min-width: @desktops) {
    padding: 0;
    padding-bottom: 40px;
  }
}
